/**
 * Position
 */

@mixin position($prefix: '') {

	.#{$prefix}pos-rel {
		position: relative;
	}

	.#{$prefix}pos-abs {
		position: absolute;
	}

	.#{$prefix}pos-fix {
		position: fixed;
	}
	
	.#{$prefix}pos-top {
		top: 0;
	}

	.#{$prefix}pos-right {
		right: 0;
	}

	.#{$prefix}pos-left {
		left: 0;
	}

	.#{$prefix}pos-bottom {
		bottom: 0;
	}

	.#{$prefix}pos-top-left {
		top: 0;
		left: 0;
	}

	.#{$prefix}pos-top-right {
		top: 0;
		right: 0;
	}

	.#{$prefix}pos-top-center {
		top: 50%;
	}

	.#{$prefix}pos-bottom-left {
		bottom: 0;
		left: 0;
	}

	.#{$prefix}pos-bottom-right {
		bottom: 0;
		right: 0;
	}

	.#{$prefix}pos-auto {
		top: auto;
		right: auto;
		bottom: auto;
		left: auto;
	}

}

@include position();