/**
 * Layer
 */

@mixin layer($prefix: '') {
	

	.#{$prefix}full {
		width: 100%;
		height: 100%;
	}

	.#{$prefix}full-width {
		width: 100%;
	}

	.#{$prefix}width-auto {
		width: auto;
	}

	.#{$prefix}height-auto {
		height: auto;
	}

	.#{$prefix}full-height {
		height: 100%;
	}

	.#{$prefix}full-pos-fix {
		@include full-pos(fixed)
	}

	.#{$prefix}full-pos-abs {
		@include full-pos(absolute)
	}

	.#{$prefix}o-hidden {
		overflow: hidden;
	}

	.#{$prefix}z-index-0 {
		z-index: 0;
	}

	.#{$prefix}z-index-1 {
		z-index: 1;
	}

	.#{$prefix}z-index-2 {
		z-index: 2;
	}

	.#{$prefix}ar-16-9 {
		@include aspect-ratio(16,9);
	}

	.#{$prefix}max-width-none {
		max-width: none;
	}

}

@include layer();