/**
 * Transform
 */

@mixin transform($prefix: '') {

	.#{$prefix}transform-center-rel {
		@include transform-center();
	}

	.#{$prefix}transform-center-abs {
		@include transform-center(absolute);
	}

	.#{$prefix}transform-center-v-rel {
		@include transform-center-v();
	}

	.#{$prefix}transform-center-v-abs {
		@include transform-center-v(absolute);
	}

	.#{$prefix}transform-center-h-rel {
		@include transform-center-h();
	}

	.#{$prefix}transform-center-h-abs {
		@include transform-center-h(absolute);
	}

}

@include transform();