@mixin transform-center($position: relative) {
	position: $position;
	top: 50%;
	left: 50%;
	transform: translateX(-50%) translateY(-50%);
}

@mixin transform-center-v($position: relative) {
	position: $position;
	top: 50%;
	transform: translateY(-50%);
}

@mixin transform-center-h($position: relative) {
	position: $position;
	left: 50%;
	transform: translateX(-50%);
}

@mixin absolute-center($width, $height) {
	position: absolute;
	top: 50%;
	left: 50%;
	margin-left: - $width / 2;
	margin-top: - $height / 2;
	width: $width;
	height: $height;
}

@mixin placeholder($color:#666, $autoreference: false) {
	
	@if $autoreference {
		&::-webkit-input-placeholder {color: $color;}
		&:-moz-placeholder {color: $color;}
		&::-moz-placeholder {color: $color;}
		&:-ms-input-placeholder {color: $color;}
	} @else {
		::-webkit-input-placeholder {color: $color;}
		:-moz-placeholder {color: $color;}
		::-moz-placeholder {color: $color;}
		:-ms-input-placeholder {color: $color;}
	}
	.placeholder{color: $color;}
}

@mixin selection($bg-color:#666, $color:#fff, $autoreference: false) {
	@if $autoreference {
		&::selection {background: $bg-color; color: $color;}
		&::-moz-selection {background: $bg-color; color: $color;}
	} @else {
		::selection {background: $bg-color; color: $color;}
		::-moz-selection {background: $bg-color; color: $color;}
	}
}

@mixin scrollbar($color:#777, $bg-color: transparent, $autoreference: false, $radius: 4px, $width: 8px) {
	@if $autoreference {
		
		&::-webkit-scrollbar {
		    width: $width;
		    height: $width;
		}

		&::-webkit-scrollbar-track {
			background: $bg-color;
		}

		&::-webkit-scrollbar-thumb {
			background: $color;
			border-radius: $radius;
		}

	} @else {
	
		::-webkit-scrollbar {
		    width: $width;
		    height: $width;
		}

		::-webkit-scrollbar-track {
			background: $bg-color;
		}

		::-webkit-scrollbar-thumb {
			background: $color;
			border-radius: $radius;
		}		

	}
}

@mixin font-face($prefix, $path: '../assets/fonts/') {
	@font-face {
		font-family: $prefix;
		src: url($path + $prefix + '/' + $prefix + '.eot');
		src: url($path + $prefix + '/' + $prefix + '.otf') format('opentype'),
			url($path + $prefix + '/' + $prefix + '.ttf') format('truetype');
		    font-weight: normal;
		    font-style: normal;
	}
}

@mixin full-pos($position: relative) {
	position: $position;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
}

@mixin alpha-overlay($color: #000, $alpha: .8) {
	background-color: rgba(red($color), green($color), blue($color), $alpha);
}

@mixin aspect-ratio($width, $height) {
  position: relative;
  &:before {
    display: block;
    content: "";
    width: 100%;
    padding-top: ($height / $width) * 100%;
  }
}