/**
 * Shadow
 */

@mixin shadow($prefix: '') {

	.#{$prefix}shadow-bottom-md {
		box-shadow: 0 12px 15px rgba(0, 0, 0, 0.15);
	}

	.#{$prefix}shadow-top-sm {
		box-shadow: 0 -5px 10px rgba(0, 0, 0, 0.1);
	}

	.#{$prefix}shadow-top-md {
		box-shadow: 0 -5px 10px rgba(0, 0, 0, 0.15);
	}

	.#{$prefix}shadow-md {
		box-shadow: 0 0 10px rgba(0, 0, 0, 0.25);
	}

	.#{$prefix}shadow-bottom-after {
		
		position: relative;
		
		&:after {
			content: '';
			position: absolute;
			left: 0;
			right: 0;
			top: 100%;
			height: 15px;
			background: linear-gradient(to bottom, rgba(0,0,0,.1) 0%,rgba(0,0,0,0) 100%);
		}
	}

	.#{$prefix}shadow-v-inner {
		position: relative;
	
		&:after,
		&:before {
			content: '';
			position: absolute;
			left: 0;
			right: 0;
			height: 15px;
		}

		&:after {
			background: linear-gradient(to top, rgba(0,0,0,.1) 0%,rgba(0,0,0,0) 100%);
			bottom: 0;
		}

		&:before {
			background: linear-gradient(to bottom, rgba(0,0,0,.1) 0%,rgba(0,0,0,0) 100%);
			top: 0;
		}
	}

	.#{$prefix}shadow-to-right-before {
		&:before {
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			background: linear-gradient(to right, rgba(0,0,0,.35) 0%,rgba(0,0,0,0) 75%);
		}
	}

	.#{$prefix}shadow-to-left-before {
		&:before {
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			background: linear-gradient(to left, rgba(0,0,0,.35) 0%,rgba(0,0,0,0) 75%);
		}
	}

	.#{$prefix}shadow-none {
		box-shadow: none;
	}	

}

@include shadow();
