/**
 * Cursor
 */

@mixin cursor($prefix: '') {

	.#{$prefix}cursor-open-hand {
		cursor: url(../../assets/cur/openhand.cur) 7 5, default;
	}

	.#{$prefix}cursor-closed-hand {
		cursor: url(../../assets/cur/closedhand.cur) 7 5, default;
	}

	.#{$prefix}cursor-pointer {
		cursor: pointer;
	}

	.#{$prefix}cursor-default {
		cursor: default;
	}

}

@include cursor();