$ctaClass: cta;

.#{$ctaClass} {

	padding: 0 10px;
	height: 40px;

	@extend .color-white;
	@extend .xs-display-block;
	@extend .xs-txt-center;
	// @extend .spacer-05;
		// @extend .cursor-pointer;
	
	user-select: none;
	display: inline-flex;
	align-items: center;
	min-width: 170px;
	vertical-align: middle;

	& > * {
		font-size: 12px;
	}

	&--red {
		background: red;
	}

	&--blue {
		background: blue;
	}

	&--green {
		background: green;
	}

	&__text {
		flex: 1;
	}

	&__icon {
		padding-left: 10px;
	}
	
}