* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

html,
body {
	line-height: 1;
	color: #ffffff;
	padding: 20px;
	height: 100%;
	-webkit-font-smoothing: antialiased;
}

a {
	text-decoration: none;
	color: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6,
strong {
	font-weight: normal;
}

h2 {
	text-transform: uppercase;
	font-family: Interstate;
	font-size: 1.2em;
	line-height: 1.2;
}

h3 {
	// font-style: italic;
	font-family: Times;
	letter-spacing: 1px;
	font-size: 1.1em;
}

.splash-container {
	max-width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
	flex-direction: column;
	text-align: center;
}

video { 
    position: fixed;
    top: 50%;
    left: 50%;
    min-width: 100%;
    min-height: 100%;
    width: auto;
    height: auto;
    z-index: -100;
    transform: translateX(-50%) translateY(-50%);
  	background-size: cover;
  	transition: 1s opacity;
}

.content-container {
	margin: auto;
	align-self: center;
}

.splash-container img {
	max-width: 300px;
}

.social-media-container{
	margin-top: auto;
}

.social-media-container i {
	padding: 0 10px;
	font-size: 1.7em;
}

