/**
 * Text
 */

@mixin txt($prefix: '') {

	.#{$prefix}txt-upper {
		text-transform: uppercase;
	}

	.#{$prefix}txt-lower {
		text-transform: lowercase;
	}

	.#{$prefix}txt-capitalize {
		text-transform: capitalize;
	}

	.#{$prefix}txt-center {
		text-align: center;
	}

	.#{$prefix}txt-left {
		text-align: left;
	}

	.#{$prefix}txt-right {
		text-align: right;
	}

	.#{$prefix}txt-underline {
		text-decoration: underline;
	}

	.#{$prefix}txt-ellipsis {
	    text-overflow: ellipsis;
	    overflow: hidden;
	    word-wrap: break-word;
	    white-space: nowrap;
	}

	.#{$prefix}txt-shadow {
		text-shadow: 5px 5px 10px rgba(black, .2);
	}

	.#{$prefix}txt-replace-br {
		br {
		    content: ' '
		}

		br:after {
		    content: ' '
		}	
	}

	.#{$prefix}txt-decoration-none {
		text-decoration: none;
	}

	.#{$prefix}txt-capitalize {
		text-transform: capitalize;
	}

	.#{$prefix}txt-l-spacing {
		letter-spacing: 2px;
	}
}

@include txt();