/**
 * Display
 */

@mixin display($prefix: '') {

	.#{$prefix}display-inline {
		display: inline;
	}

	.#{$prefix}display-inline-block {
		display: inline-block;
	}

	.#{$prefix}display-block {
		display: block;
	}

	.#{$prefix}display-none {
		display: none;
	}

	.#{$prefix}display-flex {
		display: flex;
	}

	.#{$prefix}display-inline-flex {
		display: inline-flex;
	}	

}

@include display();