/**
 * Miscellanous
 */

@mixin miscellanous($prefix: '') {

	/**
	 * Wysiwyg
	 */

	.#{$prefix}wysiwyg {
		p {
			font-size: inherit;
			font-family: inherit;
			line-height: inherit;
		}
	}

	/**
	 * CLearfix
	 */

	.#{$prefix}clearfix:after {
		visibility: hidden;
		display: block;
		font-size: 0;
		content: " ";
		clear: both;
		height: 0;
	}

	/**
	 * V align
	 */

	.#{$prefix}v-align-middle {
		display: inline-block;
		vertical-align: middle;
		float: none;
	}
	
	/**
	 * Disactive
	 */
	.#{$prefix}disactive {
		cursor: default;
		opacity: .3;
	}

	/**
	 * No wrap
	 */
	.#{$prefix}no-wrap {
		white-space: nowrap;
	}

	/**
	 * Opacity
	 */
	
	.#{$prefix}opacity-0 {
		opacity: 0;
	}

	.#{$prefix}opacity-1 {
		opacity: 1;
	}

	.#{$prefix}overflow-y-scroll {
		overflow-y: scroll;
		overflow-x: hidden;
		-webkit-overflow-scrolling: touch;
	}

	.#{$prefix}overflow-x-scroll {
		overflow-x: scroll;
		overflow-y: hidden;
		-webkit-overflow-scrolling: touch;
	}

	.#{$prefix}mask-scroll-x {
		
		-webkit-mask-image: -webkit-gradient(linear, 70% top, right top, from(rgba(0,0,0,1)), color-stop(0.75, rgba(0,0,0,1)), to(rgba(0,0,0,0)));

		margin-left: 0px;
		margin-right: 0px;

		overflow-x: scroll;
		overflow-y: hidden;
		-webkit-overflow-scrolling: touch;
		display: flex;


		& > * {
			// flex: 1;
			float: none;
			vertical-align: top;
			padding-left: 5px;
			padding-right: 5px;

			&:last-child {
				padding-right: 25px;
			}

		}

	}	

	/**
	 * CSS Timing
	 */
	

	.#{$prefix}css-timing-expo-in-out {
		transition-timing-function: cubic-bezier(0.74,-0.01, 0.09, 0.99);
	}

	.#{$prefix}css-timing-expo-out {
		transition-timing-function: cubic-bezier(0, 0.62, 0.48, 0.99);
	}

}

@include miscellanous();