/**
 * Border
 */

@mixin border($prefix: '') {

	.#{$prefix}border-none {
		border: none;
	}

	.#{$prefix}border-all {
		border: 1px solid;
	}

	.#{$prefix}border-top {
		border-top: 1px solid;
	}	

	.#{$prefix}border-bottom {
		border-bottom: 1px solid;
	}	

	.#{$prefix}border-width-2 {
		border-width: 2px;
	}	

	.#{$prefix}border-top-gray-10 {
		border-top: 1px solid lighten(black, 90%);
	}	

	.#{$prefix}border-bottom-gray-10 {
		border-bottom: 1px solid lighten(black, 90%);
	}

	.#{$prefix}border-bottom-gray-40 {
		border-bottom: 1px solid lighten(black, 60%);
	}

	.#{$prefix}border-bottom-primary {
		border-bottom: 1px solid map-get($colors, primary);
	}

	.#{$prefix}border-top-gray-40 {
		border-top: 1px solid lighten(black, 60%);
	}

	.#{$prefix}border-top-gray-90 {
		border-top: 1px solid lighten(black, 10%);
	}

	.#{$prefix}border-top-gray-80 {
		border-top: 1px solid lighten(black, 20%);
	}

	.#{$prefix}border-bottom-gray-90 {
		border-bottom: 1px solid lighten(black, 10%);
	}

	.#{$prefix}border-bottom-gray-80 {
		border-bottom: 1px solid lighten(black, 20%);
	}

	.#{$prefix}border-radius-50 {
		border-radius: 200px;
	}

	.#{$prefix}border-radius-0 {
		border-radius: 0;
	}

	@each $label, $value in $colors {
		
		.#{$prefix}border-color-#{$label} {
			border-color: $value;
		}

	}

}

@include border();