/**
 * Colors
 */

$colors: (
	"primary": red,
	"secondary": blue,
	"gray": lighten(black, 40%),
	"white": white
);

/**
 * Font size
 */

$fonts-size: (
	"xs": 0.75rem,
	"sm": 0.875rem,
	"md": 1.125rem,
	"lg": 1.5rem
);

/**
 * Line height
 */

$line-height: (
	"xs": 1.1,
	"sm": 1.2,
	"md": 1.5,
	"lg": 1.8
);

/**
 * Spacer
 */

$spacers: (
	"size": 20px,
	"multipliers": (4 3 2 1 '0.75' '0.5' '0.25' '0'),
	"margin-multipliers": (2 1 '0.75' '0.5' '0.25' '0')
);